.wrapper {
    margin-top: 50px;
}

.form {
    max-width: 330px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    background: #F9F9FE;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
}

.title {
    font-size: 50px;
}

.instructions {
    text-align: left;
    font-size: 16px;
    line-height: 24px;
    margin: 15px 0;
}